import style from '../css/index.scss'
import Map from './map/map.js';
import Page from './page/page';


var $ = require("jquery");




var app = {
    // Application Constructor
    initialize: function() {
        this.map = new Map();
        this.page = new Page();        
        this.bindEvents();
    },

    bindEvents: function() {
        this.map.on("geoLocationConfirned", () => {
            this.page.show();
        });
    }
  
};
window.app = app;

app.initialize();